<template>
  <el-form label-width="120px" :model="inspect" :rules="rules" :inline="true" ref="inspectForm">
    <el-form-item label="机构" prop="org">
      <el-select v-model="inspect.org" placeholder="请选择机构" clearable>
        <el-option v-for="item in formData.orgList" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="渠道" prop="channel">
      <el-select v-model="inspect.channel" placeholder="请选择渠道" clearable>
        <el-option v-for="item in formData.channelList" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="预算类型" prop="type">
      <el-select v-model="inspect.type" placeholder="请选择预算类型" clearable>
        <el-option v-for="item in formData.typeList" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="预算期间" prop="timeRange">
      <el-select v-model="inspect.timeRange" placeholder="请选择预算期间" clearable>
        <el-option v-for="item in timeRangeList" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="预算版本" prop="version">
      <el-select v-model="inspect.version" placeholder="请选择预算版本" clearable>
        <el-option v-for="item in formData.versionList" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
  </el-form>
</template>

<script>
import {mapState} from 'vuex'
export default {
  name: 'QueryForm',
  props: {
    inspect: {
      type: Object,
      required: true
    },
    rules: {
      type: Object,
      default: null
    }
  },
  created () {
    this.$store.dispatch('initFormData')
  },
  computed: {
    ...mapState({
      formData: state => state.inspect.formData
    }),
    inspectType () {
      return this.inspect.type
    }
  },
  watch: {
    inspectType: function (val) {
      this.inspect.timeRange = ''
      if (val !== '') {
        const obj = this.formData.typeList.find(item => item.code === val)
        this.timeRangeList = obj.timeRangeList
        if (obj.timeRangeList.length > 0) {
          this.inspect.timeRange = obj.timeRangeList[0].code
        }
      }
    }
  },
  data () {
    return {
      timeRangeList: [],
      versionList: []
    }
  }
}
</script>

<style scoped>

</style>



